.timer {
  @include timer-box();
  display: flex;

  &__img-wrapper {
    @include timer-box-wrapper();
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__img {
    width: 60px;
    height: 60px;
    margin: 5px 3px;
  }

  &__time-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__time {
    @include font-timer();
  }
}