@mixin body-background() {
  background: linear-gradient(
    180deg,
    #839371 0%,
    #526556 100%
  )
  fixed;
  // background: radial-gradient(79.22% 79.22% at 50% 20.78%, #839371 0%, #526556 100%);
}

@mixin block-box() {
  border-radius: 40px;
  background: #F6EBD5;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.40);
}

@mixin timer-box() {
  border-radius: 20px;
  border-right: 1px solid #A6B285;
  border-bottom: 1px solid #A6B285;
  background: #B5BF92;
  box-shadow: 0px 10px 20px 0px rgba(0, 0, 0, 0.40);
}

@mixin timer-box-wrapper() {
  border-right: 1px solid #A6B184;
}
